<template>
    <div class="container">
        <div class="container-title">{{type == 'add' ? '新增':'编辑'}}投票活动</div>
        <div class="form-box">
            <el-form ref="form" :model="form" label-width="140px">
                <el-form-item label="商品名称" required>
                    <el-input :disabled="disabled" v-model="form.title"></el-input>
                </el-form-item>
                <el-form-item label="商品简介">
                    <el-input :disabled="disabled" type="textarea" :autosize="{ minRows: 6, maxRows: 10}" v-model="form.description"></el-input>
                </el-form-item>
                <el-form-item label="单位" required>
                    <el-input :disabled="disabled" v-model="form.unit"></el-input>
                </el-form-item>
                <el-form-item label="兑换积分" required>
                    <el-input type="number" :disabled="disabled" v-model="form.score"></el-input>
                </el-form-item>
                <el-form-item label="库存" required>
                    <el-input type="number" :disabled="disabled" v-model="form.stock"></el-input>
                </el-form-item>
                <el-form-item label="商品轮播图" required>
                    <div>
                        <imgList />
                        <el-upload ref="upload" :action="url" multiple :file-list="fileList1" accept=".png,.jpeg,.jpg" :headers="headers" :on-success="swiperSuccess" :on-remove="swiperRemoves">
                            <el-button size="small" type="primary">点击上传</el-button>
                        </el-upload>
                    </div>
                </el-form-item>
                <el-form-item label="商品封面图" required>
                    <div>
                        <imgList />
                        <el-upload ref="upload" :action="url" multiple :file-list="fileList1" accept=".png,.jpeg,.jpg" :headers="headers" :on-success="swiperSuccess" :on-remove="swiperRemoves">
                            <el-button size="small" type="primary">点击上传</el-button>
                        </el-upload>
                    </div>
                </el-form-item>
                <el-form-item label="商品详情图">
                    <div>
                        <imgList />
                        <el-upload ref="upload" :action="url" multiple :file-list="fileList1" accept=".png,.jpeg,.jpg" :headers="headers" :on-success="swiperSuccess" :on-remove="swiperRemoves">
                            <el-button size="small" type="primary">点击上传</el-button>
                        </el-upload>
                    </div>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-show="!disabled" @click="save" :loading="loading">保存</el-button>
                    <el-button @click="back">返回</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import dev from '@/utils/dev';
import imgList from '@/components/imgList';
const USE_TYPE = {
    'COVER': 'cover', //封面图
    'THUMB': 'thumb', // 缩略图
    'SWIPER': 'swiper', // 轮播
    'DETAIL': 'detail', // 详情
};
console.log(USE_TYPE);
export default {
    components: {
        imgList,
    },
    data () {
        return {
            disabled: false,
            loading: false,
            form: {
                name: '',
                categoryId: '',
                unit: '',
                score: '',
                stock: '',
            },
            type: '',
            headers: {
                'ysxf-auth-data': this.$store.state.userData,
                'ysxf-upload-data': JSON.stringify({ category: 'shop' })
            },
            url: `${dev.URL}/file/upload/cos`,
            fileList1: []
        };
    },
    created () {
        const { type, id } = this.$route.query;
        this.type = type;
        if ((type == 'edit' || type == 'preview') && id) {
            this.id = id;
        }
        if (type == 'preview') {
            this.disabled = true;
        }
    },
    methods: {
        save () {

        },
        back () {
            this.$router.go(-1);
        },
        swiperSuccess (res) {
            console.log(res);
        },
        swiperRemoves () {

        },
        saveHandler () {
            if (!this.form.name) {
                this.$message.error('请填写商品名称');
                return false;
            }
            if (!this.form.categoryId) {
                this.$message.error('请选择商品所属分类');
                return false;
            }
            if (!this.form.unit) {
                this.$message.error('请填写商品单位');
                return false;
            }
            if (!this.form.score) {
                this.$message.error('请填写商品兑换积分');
                return false;
            }
            if (!this.form.stock) {
                this.$message.error('请填写商品库存');
                return false;
            }
            return true;
        }
    }

}
</script>

<style scoped>
.form-box {
    width: 50%;
    margin-top: 20px;
    margin-left: 10px;
}

.tag-list {
    display: flex;
    align-items: center;
}

.tag {
    margin-top: 20px;
    margin-right: 20px;
}
</style>